@-webkit-viewport   { width: device-width; }
@-moz-viewport      { width: device-width; }
@-ms-viewport       { width: device-width; }
@-o-viewport        { width: device-width; }
@viewport           { width: device-width; }
body{
	font-weight:bold;
	-webkit-text-size-adjust:100%;
	-ms-text-size-adjust:none;
	min-width:320px;
}
/*=====
General */
html, body{
	background-color:#FFF;
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	overflow-x:hidden;
	color: #666;
}

.list-inline {
	padding-left: 0;
	list-style: none;
	margin-left: -5px;
}

* {
	box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, b, strong, th {
	font-weight: bold;
}

.watermark{
	width: 100%;
	background: rgba(0, 0, 0, .4);
	position: absolute;
	z-index: 2;
}

.box{
	margin: 0 auto -90px;
	min-height: 100%;
	width: 100%;
	height: 100%;
}

.push {
	position: relative;
	background-color: #fff;
}

.footer {
  position: absolute!important;
  top: 0;
  right: 0;
  width: 100%;
  height: 90px;
  padding-top: 10px;
}

.footer > div:first-child {
  text-align: right !important;
}

.footer > div:first-child em {
  color: #869298;
}

.footer .group {
  display: inline-block;
}

.btn-panel {
  text-align: right !important;
}

.text-center {
    text-align: center;
}
form, fieldset {
    margin: 0;
    padding: 0;
    border: none;
}
.options-list {
    margin: 0 0 28px;
    padding: 0;
    list-style: none;
    letter-spacing: 0.3px;
}

.options-list li {
    display: inline-block;
    vertical-align: top;
    padding: 0 20px 10px 0;
}
.options-list.large {
    margin: 0 7px -2px 0;
}
.options-list.large .img {
    float: left;
    width: 54px;
    text-align: center;
    font-size: 14px;
    line-height: 20px;
    font-weight: normal;
}

input, textarea, select {
    font: 100% Arial, Helvetica, sans-serif;
    vertical-align: middle;
    color: #000;
}
.options-list input {
    float: left;
    margin: 6px 5px 0 0;
    padding: 0;
}
.jcf-radio {
	float:left;
	position: relative;
	overflow: hidden;
	cursor: default;
	background:#fff;
	background:url(../images/sprite_new.png) no-repeat;
	margin: 5px 8px 0 0;
	height: 12px;
	width: 12px;
}
.add-box a{
	text-decoration: underline;
}
.jcf-radio span{
	display:none;
	position:absolute;
	top:0;
	left:0;
	right:0;
	bottom:0;
	background:#5f5f5f;
	border-radius:100%;
}
.jcf-radio input[type="radio"] {
	position: absolute;
	height: 100%;
	width: 100%;
	border: 0;
	margin: 0;
	left: 0;
	top: 0;
}
.jcf-radio.jcf-checked span {display:block;}
.jcf-checkbox {
	float:left;
	position: relative;
	overflow: hidden;
	cursor: default;
	background:url(../images/sprite_new.png) no-repeat -36px -237px;
	border:0;
	margin: 5px 10px 0 0;
	height: 13px;
	width: 13px;
}

.jcf-checkbox span{
	position:absolute;
	top:0;
	left:0;
	display:none;
	height:13px;
	width:13px;
	margin:0;
	background:#222;
}

:root .jcf-checkbox span {margin:0;}
.jcf-checkbox input[type="checkbox"] {
	position: absolute;
	width: 100%;
	height: 100%;
	border: 0;
	margin: 0;
	left: 0;
	top: 0;
}

.jcf-checkbox.jcf-checked span{
	display:block;
}

/*============================================================================
Buttons */


.btn {
	display: inline-block;
	text-align: center;
	vertical-align: middle;
	touch-action: manipulation;
	cursor: pointer;
	background-image: none;
	border: 1px solid transparent;
	white-space: nowrap;
	font-size: 18px;
	line-height: 22px;
	padding: 8px 15px 10px;
	font-weight: bold;
	position: relative;
	vertical-align: top;
	text-decoration: none;
	text-transform: uppercase;
  border-radius: 0;
}

.btn-panel .btn {
	height: 30px;
	text-indent: -9999px;
	overflow: hidden;
	width: 30px;
	margin: 0 0 0 2px;
	border-top: 1px solid #999;
	font-size: 0;
  float: left;
}
@media only screen and (max-width: 1335px){
	.btn-panel{
		padding:12px 6px 4px 0;
	}
}

@media only screen and (max-width: 1023px){
	.btn-panel{
		padding:13px 6px 2px 0;
	}
	.btn-panel .btn{
		width:36px;
		height:36px;
	}
	.btn-panel .group{
		/* padding:0 14px 0 20px; */
		display: inline-block;
		vertical-align: top;
		letter-spacing: 0;
		padding: 0 7px;
	}
}

@media only screen and (max-width: 767px){
	.btn {
		font-size: 18px;
	}
}

.btn.btn-success {
	border: none;
  background-color: #199edd;
	color: #fff;
}

.btn-panel .btn:before {
	content: "";
	position: absolute;
	top: 4px;
	left: 4px;
  right: 4px;
	bottom: 4px;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
}
.btn-panel .btn-plus:before {
	background-image: url(data:image/svg+xml;base64,);
}

